import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'font-awesome/css/font-awesome.min.css';
import Nav from './components/nav.js'
import Footer from './components/footer.js'
import About from './components/about.js'

var style = {paddingTop:'100px', minHeight:'100vh', position: 'relative', width:'80vw', margin:'auto'};

export default class App extends React.Component{
    render(){
        return(
            <div style={{top:'100px' }}>
                <Nav/>
                 <div style={{position:'relative', top:'100px' }} >        
                    <div id="about" style={style} ref="about">  
                        <a name="about">
                            <About/>
                        </a>
                   </div>
                    {/* <div id="event" style={style} ref="event">  
                        <a name="event">
                            <Event/>
                        </a>
                    </div>
                    <div id="sig" style={style} ref="sig">  
                        <a name="sig">
                            <Sig/>
                        </a>
                    </div>
                    <div id="blog" style={style} ref="blog">  
                        <a name="blog">
                            <Blog/>
                        </a>
                    </div> 
                    <div id="team" style={style} ref="team">  
                        <a name="team">
                            <Team/>
                        </a>
                    </div> */}
                </div>  
                <Footer/> 
            </div>
        )
    }
}


ReactDOM.render(<App/>, document.getElementById('root'));




